import React, {useCallback, useRef, useState} from 'react';
import styles from '../Search/Search.module.scss';
import cancel from '../../assets/img/cancel.svg';
import debounce from 'lodash.debounce';
import {useDispatch, useSelector} from "react-redux";
import {selectFilter} from "../../redux/filterSlice/selectFilter";
import {filterActions} from "../../redux/filterSlice/filterSlice";


export const Search = () => {
    const dispatch = useDispatch();
    const [value, setValue] = useState('');
    const searchValue = useSelector(selectFilter);
    const inputRef = useRef<HTMLInputElement | null>(null);

    const onClickClear = () => {
        dispatch(filterActions.setSearchValue(''))
        setValue('');
        if (inputRef.current) {
            inputRef.current.focus();
        }
    }

    const updateSearchValue = useCallback(debounce((string: any) => {
        dispatch(filterActions.setSearchValue(string))
    }, 250), []);

    const onChangeInput = (event: React.ChangeEvent<HTMLInputElement>) => {
        setValue(event.currentTarget.value);

        updateSearchValue(event.currentTarget.value);
    };

    return (
        <div className={styles.root}>
            <input ref={inputRef}
                   value={value}
                   onChange={onChangeInput}
                   className={styles.input} placeholder='Search...'
            />
            {searchValue &&
                <img src={cancel}
                     alt='cancel'
                     className={styles.cancel}
                     onClick={onClickClear}
                />
            }
        </div>
    );
};