import React, {useState} from 'react';
import {useDispatch, useSelector} from "react-redux";
import {cartActions} from "../../redux/cartSlice/cartSlice";
import {selectCartItemById} from "../../redux/cartSlice/selectCart";


export type BurgerPropsType = {
    id: string;
    title: string;
    price: number;
    imageUrl: string;
    types: number[];
    rating: number;
};

const burgerTypes = ['medium', 'well-done'];

export const Burgers: React.FC<BurgerPropsType> = ({id, title, price, imageUrl, types}) => {
    const [activeType, setActiveType] = useState(0);
    const cartItem = useSelector(selectCartItemById(id));
    const dispatch = useDispatch();
    const addedCount = cartItem ? cartItem.count : 0;

    const onClickAdd = () => {
        const item = {
            id,
            title,
            price,
            imageUrl,
            type: burgerTypes[activeType],
            count: 0,
        };
        dispatch(cartActions.addItem(item));
    }

    const onClickType = (el: number) => {
        setActiveType(el)
    }

    return (
        <div className="burger-block">
            <img
                className="burger-block__image"
                src={imageUrl}
                alt="Burger"
            />
            <h4 className="burger-block__title">{title}</h4>
            <div className="burger-block__selector">
                <ul>
                    {types.map((typeId) => {
                        return(
                            <li key={typeId}
                                onClick={() => onClickType(typeId)}
                                className={activeType === typeId ? "active" : ''}
                            >
                                {burgerTypes[typeId]}
                            </li>
                        )
                    })}
                </ul>
            </div>
            <div className="burger-block__bottom">
                <div className="burger-block__price">{`${price} `}</div>
                <button onClick={onClickAdd} className="button button--outline button--add">
                    <svg
                        width="12"
                        height="12"
                        viewBox="0 0 12 12"
                        fill="none"
                        xmlns="http://www.w3.org/2000/svg"
                    >
                        <path
                            d="M10.8 4.8H7.2V1.2C7.2 0.5373 6.6627 0 6 0C5.3373 0 4.8 0.5373 4.8 1.2V4.8H1.2C0.5373 4.8 0 5.3373 0 6C0 6.6627 0.5373 7.2 1.2 7.2H4.8V10.8C4.8 11.4627 5.3373 12 6 12C6.6627 12 7.2 11.4627 7.2 10.8V7.2H10.8C11.4627 7.2 12 6.6627 12 6C12 5.3373 11.4627 4.8 10.8 4.8Z"
                            fill="white"
                        />
                    </svg>
                    <span>Add</span>
                    {addedCount > 0 && <i>{addedCount}</i>}
                </button>
            </div>
        </div>
    );
};