import {createAsyncThunk} from "@reduxjs/toolkit";
import {Burger, SearchBurgerParams} from ".//burgerTypes";
import axios from "axios";
import pickBy from 'lodash/pickBy';
import identity from 'lodash/identity';

export const fetchBurgers = createAsyncThunk<Burger[], SearchBurgerParams>(
    'burger/fetchBurgersStatus',
    async (params, thunkAPI) => {
        const { sortBy, order, category, search, currentPage } = params;
        const { data } = await axios.get<Burger[]>(`https://650ab691dfd73d1fab08bfd5.mockapi.io/items`, {
            params: pickBy(
                {
                    page: currentPage,
                    limit: 4,
                    category,
                    sortBy,
                    order,
                    search,
                },
                identity,
            ),
        });
        return data;
    },
);