import {createSlice} from "@reduxjs/toolkit";
import {BurgerSliceState, Status} from "./burgerTypes";
import {fetchBurgers} from "./asyncActionsBurger";


const initialState: BurgerSliceState = {
    items: [],
    status: Status.LOADING,
};

const burgersSlice = createSlice({
    name: "burgers",
    initialState: initialState,
    reducers: {
        setItems: (state, action) => {
            state.items = action.payload;
        },
    },
    extraReducers: (builder) => {
        builder.addCase(fetchBurgers.pending, (state, action) => {
            state.status = Status.LOADING;
            state.items = [];
        });

        builder.addCase(fetchBurgers.fulfilled, (state, action) => {
            state.items = action.payload;
            state.status = Status.SUCCESS;
        });

        builder.addCase(fetchBurgers.rejected, (state, action) => {
            state.status = Status.ERROR;
            state.items = [];
        });
    },
});

export const burgersActions = burgersSlice.actions;
export const burgersReducer = burgersSlice.reducer;